	module.exports = {"data":{"markdownRemark":{"html":"<p>今日の国民の日、北京の国民の日のパーティーは、非常に暖かく、壮大で、これまで以上に熱狂的であるようです。正方形のライトは明るく、カラフルな花、群衆、喜び、笑い、歓喜の敬礼、花火、踊り、歌、10の風です。今年の花火は特に高いですが、特に良いです。過去には見られなかった種類もあり、その量も多かったです。空では、大地を反映して空を映し出して、とても美しい服が生まれました。</p>\n<p>国家は国家の誠実さと国家の偉大さを感じるための最良の方法です。全国の個々の分子は、しばしば日常生活に散らばっている、または小グループでは、全体的な感情は強くない。</p>\n<p>国民の日々には、文化的、経済的、芸術的なつながりがあるだけであり、国全体の儀式は人々にもっと重要なことを伝えることができます。したがって、世界の多くの国々は自国の儀式に大きな関心を払い、国民の日が最善の機会です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0152.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0152.md","frontmatter":{"title":"10月1日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0152.md absPath of file >>> MarkdownRemark"}}

